@import url("https://fonts.googleapis.com/css2?family=Inter:ital,wght@0,200;0,300;0,400;1,300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Manrope:wght@300&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-inter: Inter;
  --font-manrope: Manrope;

  /* font sizes */
  --font-size-11xl: 30px;
  --font-size-140xl: 159px;
  --font-size-5xl: 24px;
  --font-size-53xl: 72px;
  --font-size-lg: 18px;

  /* Colors */
  --color-gray-100: rgba(0, 0, 0, 0.2);
  --color-gray-200: rgba(0, 0, 0, 0.53);
  --color-black: #000;
  --color-oldlace: #f5f4e4;

  /* Gaps */
  --gap-4xs: 9px;

  /* Paddings */
  --padding-10xs: 3px;
  --padding-36xl-3: 55.3px;
  --padding-38xl-6: 57.6px;
  --padding-8xs-2: 4.2px;
  --padding-8xs: 5px;
  --padding-7xs-2: 5.2px;
  --padding-9xs: 4px;
  --padding-8xs-9: 4.9px;

  /* border radiuses */
  --br-41xl: 60px;
}
